import * as Tone from 'tone'
import { CellularAutomata1D } from "../cellular-automata";
import { Chord } from "@tonaljs/tonal";

const NOTE_DURATION = 5;

export class CellularAutomata1DPlayer {
    private note = "";
    private lastNote = "";
    private voice = "";
    private lastVoice = "";
    private currentChord = "";
    private chords = [""];
    private playedChords = [] as string[]
    private chordsTracker = 0;
    private playedNotes = [] as string[]
    private notesTracker = 0;
    private instrument: Tone.Sampler;
    private automata: CellularAutomata1D;
    private chordsDuration;
    private voicesDuration;
    private notesDuration;
    private chordsOctave;
    private voicesOctave;
    private notesOctave;
    private rythmMarker;
    private step = 0;

    constructor(instrument: Tone.Sampler,
        automata: CellularAutomata1D,
        chordsOctave: number,
        voicesOctave: number,
        notesOctave: number,
        chordsDuration: number,
        voicesDuration: number,
        notesDuration: number,
        chords: string[],
        rythmMarker: number
    ) {
        this.instrument = instrument;
        this.automata = automata;
        this.chordsOctave = chordsOctave;
        this.voicesOctave = voicesOctave;
        this.notesOctave = notesOctave;
        this.chordsDuration = chordsDuration;
        this.notesDuration = notesDuration;
        this.voicesDuration = voicesDuration;
        this.rythmMarker = rythmMarker;
        this.chords = chords;

    }

    play() {
        this.playChord();
        this.playVoice();
        this.playNote();

        this.step++;
    }

    private playChord() {
        if (this.step % this.chordsDuration != 0) {
            return;
        }

        if (this.playedChords.length == this.rythmMarker) {
            this.currentChord = this.playedChords[this.chordsTracker % this.playedChords.length];
            this.chordsTracker++;
        } else {
            let targetIndex = this.leeDistance(0) % (this.chords.length * 2);
            let actualIndex = targetIndex >= this.chords.length ? this.chords.length - (targetIndex % this.chords.length) - 1 : targetIndex;
            this.currentChord = this.chords[actualIndex % this.chords.length];
            this.playedChords.push(this.currentChord);
        }

        let midiNotes = Chord.get(this.currentChord).notes.map((note) => Tone.Frequency(note).transpose(this.chordsOctave * 12).toFrequency())
        this.instrument.triggerAttackRelease(midiNotes, NOTE_DURATION, undefined, 0.15);
    }

    private playVoice() {
        if (this.leeDistance(1) == 0 || (this.step % this.voicesDuration) != 0) {
            return;
        }

        let notes = Chord.get(this.currentChord).notes;
        let targetIndex = this.leeDistance(1) % (notes.length * 2);
        let actualIndex = targetIndex >= notes.length ? notes.length - (targetIndex % notes.length) - 1 : targetIndex;
        this.voice = notes[actualIndex % notes.length];

        if (this.voice != this.lastVoice) {
            let midiNote = Tone.Frequency(this.voice).transpose(this.voicesOctave * 12).toFrequency();
            this.instrument.triggerAttackRelease(midiNote, NOTE_DURATION, undefined, 0.4);
            this.lastVoice = this.voice;
        }

    }

    private playNote() {
        let state = this.automata.states == 3 ? 2 : 0;
        if (this.leeDistance(state) == 0 || this.step % this.notesDuration != 0) {
            return;
        }

        if (this.playedChords.length == this.rythmMarker) {
            this.note = this.playedNotes[this.notesTracker % this.playedNotes.length];
            this.notesTracker++;
        } else {
            let notes = Chord.get(this.currentChord).notes;
            let targetIndex = this.leeDistance(state) % (notes.length * 2);
            let actualIndex = targetIndex >= notes.length ? notes.length - (targetIndex % notes.length) - 1 : targetIndex;
            this.note = notes[actualIndex % notes.length];
            this.playedNotes.push(this.note);
        }

        if (this.note != this.lastNote) {
            let midiNote = Tone.Frequency(this.note).transpose(this.notesOctave * 12).toFrequency();
            this.instrument.triggerAttackRelease(midiNote, NOTE_DURATION, undefined, 0.2);
            this.lastNote = this.note;
        }

    }

    private leeDistance(state: number) {
        return this.automata.state.reduce((acc, _, index) => {
            let euclideanDistance = Math.abs(this.automata.state[index] - this.automata.previousState[index])
            let leeDistance = Math.min(euclideanDistance, this.automata.states - euclideanDistance)
            return this.automata.state[index] != state ? acc : acc + leeDistance;
        }, 0);
    }

    stop() {
        this.instrument.releaseAll();
        this.instrument.dispose();
        this.note = "";
        this.lastNote = "";
        this.step = 0;
    }

    static Builder = class {
        private automata?: CellularAutomata1D;
        private chords = [
            ["C0major", "F0major", "G0major", "D0m", "A0m", "E0m"],
            ["C0major", "F0major", "A0m", "E0m"],
            ["C0major", "G0major", "D0m", "E0m"],
            ["G0major", "C0major", "D0major", "A0m", "E0m", "B0m"],
            ["G0major", "C0major", "E0m", "B0m"],
            ["G0major", "D0major", "A0m", "B0m"],
            ["D0major", "G0major", "A0major", "E0m", "B0m", "F#0m"],
            ["D0major", "G0major", "B0m", "F#0m"],
            ["D0major", "A0major", "E0m", "F#0m"],
            ["A0major", "D0major", "E0major", "B0m", "F#0m", "C#0m"],
            ["A0major", "D0major", "F#0m", "C#0m"],
            ["A0major", "E0major", "B0m", "C#0m"],
            ["E0major", "A0major", "B0major", "F#0m", "C#0m", "G#0m"],
            ["E0major", "A0major", "C#0m", "G#0m"],
            ["E0major", "B0major", "F#0m", "G#0m"],
            ["B0major", "E0major", "F#0major", "C#0m", "G#0m", "Eb0m"],
            ["B0major", "E0major", "G#0m", "Eb0m"],
            ["B0major", "F#0major", "C#0m", "Eb0m"],
            ["F#0major", "B0major", "C#0major", "G#0m", "Eb0m", "Bb0m"],
            ["F#0major", "B0major", "Eb0m", "Bb0m"],
            ["F#0major", "C#0major", "G#0m", "Bb0m"],
            [
                "C0major", "C0maj7",
                "D0m", "D0m7",
                "E0m", "E0m7",
                "F0major", "F0maj7",
                "G0major", "G0 7",
                "A0m", "A0m7",
                "B0dim", "B0m7b5"
            ]
        ];

        withAutomata(automata: CellularAutomata1D) {
            this.automata = automata;
            return this;
        }

        async build() {
            if (this.automata === null) {
                throw new Error("Must pass a cellular automata upon building");
            }
            const instrument = new Tone.Sampler({
                urls: {
                    "C1": "C1.mp3",
                    "D#1": "Ds1.mp3",
                    "F#1": "Fs1.mp3",
                    "A1": "A1.mp3",
                    "C2": "C2.mp3",
                    "D#2": "Ds2.mp3",
                    "F#2": "Fs2.mp3",
                    "A2": "A2.mp3",
                    "C3": "C3.mp3",
                    "D#3": "Ds3.mp3",
                    "F#3": "Fs3.mp3",
                    "A3": "A3.mp3",
                    "C4": "C4.mp3",
                    "D#4": "Ds4.mp3",
                    "F#4": "Fs4.mp3",
                    "A4": "A4.mp3",
                    "C5": "C5.mp3",
                    "D#5": "Ds5.mp3",
                    "F#5": "Fs5.mp3",
                    "A5": "A5.mp3",
                },
                baseUrl: "https://tonejs.github.io/audio/salamander/",
            }).toDestination();

            await Tone.loaded();

            let chord = this.chords[Math.floor(Math.random() * this.chords.length)];
            let chordOctave = Math.round(Math.random() * 2) + 3;
            let voiceOctave = Math.round(Math.random() * 2) + 4;
            let noteOctave = Math.round(Math.random() * 2) + 3;

            let longDurations = [3, 4, 6, 8, 10];
            let shortDurations = [1, 2, 3, 4, 5];

            let chordDuration = longDurations[Math.floor(Math.random() * longDurations.length)]
            let voicesDuration = shortDurations[Math.floor(Math.random() * shortDurations.length)]
            let notesDuration = shortDurations[Math.floor(Math.random() * shortDurations.length)]

            let rythmMarkers = [2, 3, 4, 5];
            let rythmMarker = rythmMarkers[Math.floor(Math.random() * rythmMarkers.length)];
            return new CellularAutomata1DPlayer(
                instrument,
                this.automata!,
                chordOctave,
                voiceOctave,
                noteOctave,
                chordDuration,
                voicesDuration,
                notesDuration,
                chord,
                rythmMarker
            );
        }
    }
}